import dayjs from 'dayjs';
import {
  ChatRoom,
  Contact,
  ContactGather,
  Group,
  GroupGather,
} from '../types';

export function isEmpty(value: unknown): boolean {
  return (
    value == null ||
    (typeof value === 'object' && Object.keys(value).length === 0) ||
    (typeof value === 'string' && value.trim().length === 0)
  );
}

// string contains string
export function isContainStr(str1: string, str2: string): boolean {
  return str2.indexOf(str1) >= 0;
}

/**
 * check URL
 * @param text
 */
export function isUrl(text: string): boolean {
  // parse url
  const UrlReg = new RegExp(
    /http(s)?:\/\/([\w-]+\.)+[\w-]+(\/[\w- ./?%&=]*)?/,
  );
  return UrlReg.test(text);
}

/**
 * –§–æ—Ä–º–∏—Ç–∏—Ä–æ–≤–∞–Ω–∏–µ –≤—Ä–µ–º–µ–Ω–∏ —Å–æ–æ–±—â–µ–Ω–∏—è
 * @param time
 */
export function formatTime(
  time: Date | string | undefined,
  format = 'DD.MM.YYYY HH:mm',
) {
  if (typeof time === 'undefined') return null;
  if (typeof time === 'string') time = new Date(time);
  // –±–æ–ª—å—à–µ —á–µ–º –≤—á–µ—Ä–∞
  if (
    dayjs()
      .add(-1, 'days')
      .startOf('day')
      .isAfter(time)
  ) {
    return dayjs(time).format(format);
  }
  // –≤—á–µ—Ä–∞
  if (
    dayjs()
      .startOf('day')
      .isAfter(time)
  ) {
    return `–í—á–µ—Ä–∞ –≤ ${dayjs(time).format('HH:mm')}`;
  }

  return dayjs(time).format('HH:mm');
}

/**
 * –†–∞—Å–∫—Ä—ã—Ç—å —Å–æ–¥–µ—Ä–∂–∏–º–æ–µ
 * @param content - –¥–∞–Ω–Ω—ã–µ –≤ —Å—Ç—Ä–æ–∫–µ
 */
export function getFileMeta(
  content: string,
): {
  date: string;
  userId: string;
  size: string;
  name: string;
} {
  // –§–æ—Ä–º–∞—Ç  [date]$[userId]$[size]$[fileName]
  // –ù–∞–ø—Ä–∏–º–µ—Ä fileName = 1606980397047$1a01e20f-3780-4227-84b5-5c69ca766ee5$15.41KB$123.docx
  const meta = content.split('$');
  const [date, userId, size, name] = meta;
  return {
    date,
    userId,
    size,
    name,
  };
}

export function getImageMeta(
  content: string,
): {
  date: string;
  userId: string;
  width: string;
  height: string;
} {
  // –§–æ—Ä–º–∞—Ç [date]$[userId]$[width]$[height]$...
  const meta = content.split('$');
  const [date, userId, width, height] = meta;
  return {
    date,
    userId,
    width,
    height,
  };
}

export function splitFileName(
  name: string,
): { name: string; ext: string } {
  const idx = name.lastIndexOf('.');
  if (idx === -1)
    return {
      name,
      ext: '',
    };
  return {
    name: name.slice(0, idx),
    ext: name.slice(idx + 1),
  };
}

export const getChatId = (chat: ChatRoom | null): string | null => {
  if (!chat) return null;
  return (chat as Group).groupId
    ? `group:${(chat as Group).groupId}`
    : `user:${chat.userId}`;
};

export const getChatName = (chat: ChatRoom): string => {
  return (chat as Group).groupId
    ? (chat as Group).name
    : (chat as Contact).username;
};

export const allMessCount = (chats: GroupGather | ContactGather) => {
  return Object.values(chats)
    .map(it => (it?.messages != null ? it?.messages.length : 0))
    .reduce((a: number, b: number) => a + b, 0);
};

export const chatRoomComparer = (
  a: ChatRoom,
  b: ChatRoom,
): number => {
  const hasMessagesA =
    Array.isArray(a.messages) && a.messages.length > 0;
  const hasMessagesB =
    Array.isArray(b.messages) && b.messages.length > 0;
  if (
    hasMessagesA &&
    hasMessagesB &&
    b.messages != null &&
    a.messages != null
  ) {
    // !!! if cdate === undefined !!!
    const bb =
      b.messages[b.messages.length - 1].cdate != null
        ? new Date(b.messages[b.messages.length - 1].cdate).getTime()
        : new Date().getTime();

    const aa =
      a.messages[a.messages.length - 1].cdate != null
        ? new Date(a.messages[a.messages.length - 1].cdate).getTime()
        : new Date().getTime() - 1;
    const res = bb - aa;

    //console.log('res', res);
    return res;
  }
  if (hasMessagesA) {
    return -1;
  }
  return 1;
};

export const getParam = (param: string) => {
  const QueryString = window.location.search;
  const urlParams = new URLSearchParams(QueryString);
  return urlParams.get(param);
};

export const combineURLs = (
  baseURL: string,
  relativeURL: string,
  queryParams?: Record<string, string>,
): string => {
  const url = relativeURL
    ? baseURL.replace(/\/?\/$/, '') +
      '/' +
      relativeURL.replace(/^\/+/, '')
    : baseURL;
  if (!queryParams) return url;

  return (
    url +
    (url.includes('?') ? '&' : '?') +
    new URLSearchParams(queryParams).toString()
  );
};
