import React from 'react';
import {
  AddGroupMessages,
  AddPrivateMessages,
  ChatRoom,
  Contact,
  ContactGather,
  Group,
  GroupGather,
  GroupMember,
  GroupMessage,
  MessageOperation,
  PrivateMessage,
  SetActiveRoom,
  //SetUser,
  SetTyping,
  User,
  ConferenceData,
  GroupMap,
  VisitData,
} from '../types';
import { chatRoomComparer } from '../utils/common';

export interface ChatState {
  user: User;
  token: string;
  refreshToken: string;
  activeRoom: ChatRoom | null;
  chatOld: ChatRoom | null;

  groupGather: GroupGather;
  userGather: ContactGather;
  contactGather: ContactGather;
  operators: Contact[];
  initialContactId: number | null;
  conference: {
    data: ConferenceData | null;
    joined: boolean;
    ringPlayed: boolean;
  };
  typing: SetTyping | null;
  loading: boolean;
  error?: string;
  success?: string;
  visitData: VisitData[];
}

const emptyUser: User = {
  userId: 0,
  username: '',
  password: '',
  avatar: '',
  langCode: '',
};

const emptyChatState: ChatState = {
  user: emptyUser, // –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å
  token: '', // —Ç–æ–∫–µ–Ω
  refreshToken: '',
  activeRoom: null, // –∞–∫—Ç–∏–≤–Ω–∞—è –∫–æ–º–Ω–∞—Ç–∞
  chatOld: null, // –∞–∫—Ç–∏–≤–Ω–∞—è –∫–æ–º–Ω–∞—Ç–∞

  groupGather: {}, // —Å–ø–∏—Å–æ–∫ –≥—Ä—É–ø–ø
  userGather: {}, // —Å–ø–∏—Å–æ–∫ –æ–Ω–ª–∞–π–Ω
  contactGather: {}, // –∫–æ–Ω—Ç–∞–∫—Ç—ã
  operators: [], // –æ–ø–µ—Ä–∞—Ç–æ—Ä—ã
  initialContactId: null, // –Ω–∞—á–∞–ª—å–Ω—ã–π –∫–æ–Ω—Ç–∞–∫—Ç
  conference: {
    data: null, // –¥–∞–Ω–Ω—ã–µ –∫–æ–Ω—Ñ–µ—Ä–µ–Ω—Ü–∏–∏
    joined: false,
    ringPlayed: false,
  },
  typing: null, // –∫—Ç–æ –ø–µ—á–∞—Ç–∞–µ—Ç —Ç–µ–∫—Å—Ç
  loading: false, // –∑–∞–≥—Ä—É–∑–∫–∞ –¥–∞–Ω–Ω—ã—Ö
  error: undefined, // –æ—à–∏–±–∫–∞
  success: undefined, // –ù–ï –æ—à–∏–±–∫–∞
  visitData: [],
};

type ChatActionType =
  | 'ADD_GROUP_MEMBER'
  | 'ADD_GROUP_MESSAGE'
  | 'ADD_GROUP_MESSAGES'
  | 'SET_GROUP_MESSAGES'
  | 'ADD_PRIVATE_MESSAGE'
  | 'ADD_PRIVATE_MESSAGES'
  | 'SET_PRIVATE_MESSAGES'
  | 'SET_ACTIVE_ROOM'
  | 'UPDATE_ACTIVE_ROOM'
  | 'SET_GROUP_GATHER'
  | 'SET_CONTACT_GATHER'
  | 'SET_USER_GATHER'
  | 'DEL_GROUP'
  | 'DEL_GROUP_MEMBER'
  | 'DEL_CONTACT'
  | 'ADD_CONTACT_UNREAD_GATHER'
  | 'LOSE_CONTACT_UNREAD_GATHER'
  | 'ADD_GROUP_UNREAD_GATHER'
  | 'LOSE_GROUP_UNREAD_GATHER'
  | 'REVOKE_MESSAGE'
  | 'USER_ONLINE'
  | 'USER_OFFLINE'
  | 'UPDATE_USER_INFO'
  | 'SET_INITIAL_CONTACT_ID'
  | 'SET_CONFERENCE'
  | 'JOIN_CONFERENCE'
  | 'PAUSE_CONFERENCE'
  | 'STOP_CONFERENCE'
  | 'SET_TYPING'
  | 'MARK_AS_READ'
  | 'UPDATE_GROUP_INFO'
  | 'MARK_PRIVATE_MESSAGES_READ'
  | 'SET_LOADING'
  | 'SET_ERROR'
  | 'SET_SUCCES'
  | 'SET_USER'
  | 'SET_TOKEN'
  | 'CLEAR_USER'
  | 'CLEAR_CHAT_DATA'
  | 'SET_OPERATORS'
  | 'SET_VISIT_DATA';

type Action = {
  type: ChatActionType;
  payload?:
    | null
    | boolean
    | number
    | string
    | Group
    | Contact
    | User
    | PrivateMessage
    | GroupMessage
    | SetTyping
    | MessageOperation
    | GroupMember
    | AddPrivateMessages
    | AddGroupMessages
    | SetActiveRoom
    | ConferenceData
    | Contact[]
    | GroupMap
    | VisitData[];
};

const getFreshActiveRoom = (state: ChatState) => {
  if (state.activeRoom)
    return (
      state.groupGather[(state.activeRoom as Group).groupId] ||
      state.contactGather[state.activeRoom.userId]
    );
  return null;
};

const getActiveRoom = (state: ChatState) => {
  const { activeRoom, initialContactId, contactGather } = state;
  let newActiveRoom: ChatRoom | null = null;
  if (initialContactId) {
    newActiveRoom = contactGather[initialContactId];
  } else if (activeRoom) {
    newActiveRoom = getFreshActiveRoom(state);
  } else {
    // –∏—â–µ–º –∫–æ–º–Ω–∞—Ç—É —Å —Å–∞–º—ã–º —Å–≤–µ–∂–∏–º —Å–æ–æ–±—â–µ–Ω–∏–µ–º
    const rooms = [
      ...Object.values(state.contactGather),
      ...Object.values(state.groupGather),
    ].sort(chatRoomComparer);
    if (rooms.length > 0) newActiveRoom = rooms[0];
  }
  return newActiveRoom;
};

const setUserOnline = (
  state: ChatState,
  userId: number,
  online: 0 | 1,
): ChatState => {
  const newState = { ...state };
  // –û–±–Ω–æ–≤–∏—Ç—å —Å—Ç–∞—Ç—É—Å—ã –ø—Ä–∏–≤–∞—Ç–Ω—ã—Ö —á–∞—Ç–æ–≤
  if (state.contactGather[userId])
    newState.contactGather[userId] = {
      ...newState.contactGather[userId],
      online,
    };

  // –û–±–Ω–æ–≤–∏—Ç—å —Å—Ç–∞—Ç—É—Å —É—á–∞—Å—Ç–Ω–∏–∫–∞ –≤ –≥—Ä—É–ø–ø–∞—Ö
  for (const group of Object.values(state.groupGather) as Group[]) {
    if (!group.members) continue;
    const member = group.members.find(m => m.userId === userId);
    if (member) {
      const index = group.members.indexOf(member);
      group.members[index] = { ...member, online };
    }
  }
  // –û–±–Ω–æ–≤–∏—Ç—å —Å—Ç–∞—Ç—É—Å –æ–ø–µ—Ä–∞—Ç–æ—Ä–æ–≤
  const idx = newState.operators.findIndex(
    it => it.userId === userId,
  );
  if (idx !== -1)
    newState.operators[idx] = { ...newState.operators[idx], online };
  // –æ–±–Ω–æ–≤–ª—è–µ–º –∞–∫—Ç–∏–≤–Ω—ã–π —á–∞—Ç
  newState.activeRoom = getFreshActiveRoom(newState);
  return newState;
};

const addGroupMessage = (state: ChatState, payload: GroupMessage) => {
  const newState = { ...state };
  const { groupId } = payload;
  if (newState.groupGather[groupId]) {
    if (newState.groupGather[groupId].messages) {
      newState.groupGather[groupId].messages = [
        ...(newState.groupGather[groupId].messages as GroupMessage[]),
        payload,
      ];
    } else {
      newState.groupGather[groupId] = {
        ...state.groupGather[groupId],
        messages: [payload],
      };
    }
  }

  // —É–≤–µ–ª–∏—á–∏–≤–∞–µ–º —Å—á–µ—Ç—á–∏–∫ –Ω–æ–≤—ã—Ö —Å–æ–æ–±—â–µ–Ω–∏–π, –µ—Å–ª–∏ —ç—Ç–æ –Ω–µ –∞–∫—Ç–∏–≤–Ω–∞—è –∫–æ–º–Ω–∞—Ç–∞ –∏ —Å–æ–æ–±—â–µ–Ω–∏–µ –Ω–µ –æ—Ç –Ω–∞—Å
  const { activeRoom } = newState;
  if (
    activeRoom &&
    (activeRoom as Group).groupId !== groupId &&
    payload.userId !== state.user.userId
  ) {
    return groupUnreadGather(
      newState,
      groupId,
      (x?: number) => (x || 0) + 1,
    );
  }

  // –æ–±–Ω–æ–≤–ª—è–µ–º –∞–∫—Ç–∏–≤–Ω—ã–π —á–∞—Ç
  newState.activeRoom = getFreshActiveRoom(newState);
  return newState;
};

const addPrivateMessage = (
  state: ChatState,
  payload: PrivateMessage,
) => {
  const newState = { ...state };
  const contactId =
    payload.contactId === state.user.userId
      ? payload.userId
      : payload.contactId;

  // 1 –¥–æ–±–∞–≤–ª—è–µ–º —Å–æ–æ–±—â–µ–Ω–∏–µ
  if (newState.contactGather[contactId].messages) {
    newState.contactGather[contactId].messages = [
      ...(newState.contactGather[contactId]
        .messages as PrivateMessage[]),
      payload,
    ];
  } else {
    newState.contactGather[contactId] = {
      ...newState.contactGather[contactId],
      messages: [payload],
    };
  }

  // 2 –µ—Å–ª–∏ —ç—Ç–æ —Å–æ–æ–±—â–µ–Ω–∏–µ –≤ –Ω–µ–∞–∫—Ç–∏–≤–Ω–æ–π –∫–æ–º–Ω–∞—Ç–µ –∏ –∏—Å—Ç–æ—á–Ω–∏–∫ –Ω–µ –º—ã (–≤ —Å–æ—Å–µ–¥–Ω–µ–π –≤–∫–ª–∞–¥–∫–µ), —Ç–æ —É–≤–µ–ª–∏—á–∏–≤–∞–µ–º —Å—á–µ—Ç—á–∏–∫ –Ω–µ–ø—Ä–æ—á–∏—Ç–∞–Ω–Ω—ã—Ö
  if (
    newState.activeRoom?.userId !== contactId &&
    payload.userId !== state.user.userId
  ) {
    return contactUnreadGather(
      newState,
      contactId,
      (x?: number) => (x || 0) + 1,
    );
  }

  // –æ–±–Ω–æ–≤–ª—è–µ–º –∞–∫—Ç–∏–≤–Ω—ã–π —á–∞—Ç
  newState.activeRoom = getFreshActiveRoom(newState);
  return newState;
};

const contactUnreadGather = (
  state: ChatState,
  userId: number,
  predicate: (x?: number) => number,
) => {
  const newState = { ...state };

  if (newState.contactGather[userId]) {
    newState.contactGather[userId] = {
      ...newState.contactGather[userId],
      unreadCount: predicate(
        newState.contactGather[userId].unreadCount,
      ),
    };
  }

  // –æ–±–Ω–æ–≤–ª—è–µ–º –∞–∫—Ç–∏–≤–Ω—ã–π —á–∞—Ç
  newState.activeRoom = getFreshActiveRoom(newState);

  return newState;
};

const groupUnreadGather = (
  state: ChatState,
  groupId: number,
  predicate: (x?: number) => number,
) => {
  const newState = { ...state };

  if (newState.groupGather[groupId]) {
    newState.groupGather[groupId] = {
      ...newState.groupGather[groupId],
      unreadCount: predicate(
        newState.groupGather[groupId].unreadCount,
      ),
    };
  }

  // –æ–±–Ω–æ–≤–ª—è–µ–º –∞–∫—Ç–∏–≤–Ω—ã–π —á–∞—Ç
  newState.activeRoom = getFreshActiveRoom(newState);

  return newState;
};

const revokeMessage = (
  state: ChatState,
  payload: MessageOperation,
) => {
  const { userId } = state.user;
  const newState = { ...state };
  const userName =
    payload.username ||
    newState.userGather[payload.userId as number].username;
  if (payload.groupId) {
    const { messages } = newState.groupGather[payload.groupId];
    // –∑–∞–¥–∞–µ–º isRevoke
    if (messages) {
      const msg = messages.find(
        message => message._id === payload._id,
      );
      if (msg) {
        const index = messages.indexOf(msg);
        messages[index] = {
          ...msg,
          isRevoke: true,
          revokeUserName: userName,
        };
      }
    }
  } else {
    const { messages } = newState.contactGather[
      payload.contactId === userId
        ? (payload.userId as number)
        : (payload.contactId as number)
    ];
    // –∑–∞–¥–∞–µ–º isRevoke
    if (messages) {
      const msg = messages.find(
        message => message._id === payload._id,
      );
      if (msg) {
        const index = messages.indexOf(msg);
        messages[index] = {
          ...msg,
          isRevoke: true,
          revokeUserName: userName,
        };
      }
    }
  }
  // –æ–±–Ω–æ–≤–ª—è–µ–º –∞–∫—Ç–∏–≤–Ω—ã–π —á–∞—Ç
  newState.activeRoom = getFreshActiveRoom(newState);
  return newState;
};

const delContact = (state: ChatState, userId: number) => {
  const newState = { ...state };
  const updateActiveRoom =
    newState.activeRoom === newState.contactGather[userId];
  delete newState.contactGather[userId];
  if (updateActiveRoom) newState.activeRoom = getActiveRoom(newState);

  return newState;
};

const delGroup = (state: ChatState, groupId: number) => {
  const newState = { ...state };
  const updateActiveRoom =
    newState.activeRoom === newState.groupGather[groupId];
  delete newState.groupGather[groupId];
  if (updateActiveRoom) newState.activeRoom = getActiveRoom(newState);

  return newState;
};

const delGroupMember = (state: ChatState, data: GroupMap) => {
  const newState = { ...state };

  const group = newState.groupGather[data.groupId];
  if (group) {
    group.members = group.members?.filter(
      it => it.userId !== data.userId,
    );
  }

  return newState;
};

const updateGroupInfo = (state: ChatState, group: Group) => {
  const newState = { ...state };

  const { groupId, name, notice } = group;
  const info = newState.groupGather[groupId];
  if (info) {
    newState.groupGather[groupId] = { ...info, name, notice };
  }

  // –æ–±–Ω–æ–≤–ª—è–µ–º –∞–∫—Ç–∏–≤–Ω—ã–π —á–∞—Ç
  newState.activeRoom = getFreshActiveRoom(newState);

  return newState;
};

const updateUserInfo = (state: ChatState, user: User) => {
  const newState = { ...state };

  const { userId, username, avatar } = user;

  if (newState.userGather[userId]) {
    newState.userGather[userId] = {
      ...newState.userGather[userId],
      username,
      avatar,
    };
  }

  if (newState.contactGather[userId]) {
    newState.contactGather[userId] = {
      ...newState.contactGather[userId],
      username,
      avatar,
    };
  }

  // –æ–±–Ω–æ–≤–ª—è–µ–º –∞–∫—Ç–∏–≤–Ω—ã–π —á–∞—Ç
  newState.activeRoom = getFreshActiveRoom(newState);

  return newState;
};

const addGroupMember = (state: ChatState, payload: GroupMember) => {
  const members: Contact[] = payload.members.map(member => ({
    ...member,
    isManager: 0,
  }));

  const newState = { ...state };

  if (newState.groupGather[payload.groupId].members && members) {
    newState.groupGather[payload.groupId].members = [
      ...(state.groupGather[payload.groupId].members as Contact[]),
      ...members,
    ];
  } else {
    newState.groupGather[payload.groupId] = {
      ...newState.groupGather[payload.groupId],
      members,
    };
  }

  // –æ–±–Ω–æ–≤–ª—è–µ–º –∞–∫—Ç–∏–≤–Ω—ã–π —á–∞—Ç
  newState.activeRoom = getFreshActiveRoom(newState);
  return newState;
};

const markPrivateMessagesRead = (
  state: ChatState,
  userId: number,
) => {
  const newState = { ...state };

  if (newState.contactGather[userId]) {
    const updatedValue = { ...newState.contactGather[userId] };

    if (updatedValue.messages) {
      for (let i = 0; i < updatedValue.messages.length; i++)
        updatedValue.messages[i] = {
          ...updatedValue.messages[i],
          status: 1,
        };
    }
    newState.contactGather[userId] = updatedValue;
  }

  // –æ–±–Ω–æ–≤–ª—è–µ–º –∞–∫—Ç–∏–≤–Ω—ã–π —á–∞—Ç
  newState.activeRoom = getFreshActiveRoom(newState);

  return newState;
};

const addPrivateMessages = (
  state: ChatState,
  data: AddPrivateMessages,
) => {
  const newState = { ...state };

  const { messages, contactId } = data;

  if (newState.contactGather[contactId]) {
    newState.contactGather[contactId] = {
      ...newState.contactGather[contactId],
      messages: [
        ...(messages || []),
        ...(newState.contactGather[contactId].messages || []),
      ],
      noMoreData: messages?.length
        ? messages?.length < data.pageSize
        : false,
    };
  }

  // –æ–±–Ω–æ–≤–ª—è–µ–º –∞–∫—Ç–∏–≤–Ω—ã–π —á–∞—Ç
  newState.activeRoom = getFreshActiveRoom(newState);

  return newState;
};

const addGroupMessages = (
  state: ChatState,
  data: AddGroupMessages,
) => {
  const newState = { ...state };

  const { groupId, messages, userArr: users } = data;
  if (newState.groupGather[groupId]) {
    newState.groupGather[groupId] = {
      ...newState.groupGather[groupId],
      messages: [
        ...(messages || []),
        ...(newState.groupGather[groupId].messages || []),
      ],
      noMoreData: messages?.length
        ? messages?.length < data.pageSize
        : false,
    };
  }

  newState.userGather = { ...newState.userGather };
  for (const user of users) {
    if (!newState.userGather[user.userId]) {
      newState.userGather[user.userId] = user;
    }
  }

  // –æ–±–Ω–æ–≤–ª—è–µ–º –∞–∫—Ç–∏–≤–Ω—ã–π —á–∞—Ç
  newState.activeRoom = getFreshActiveRoom(newState);

  return newState;
};

const setActiveRoom = (state: ChatState, data: SetActiveRoom) => {
  //if (state.activeRoom && data.ifNotExists) return state;
  return {
    ...state,
    chatOld:
      state.activeRoom != null ? { ...state.activeRoom } : null,
    activeRoom: data.groupId
      ? state.groupGather[data.groupId]
      : data.contactId
      ? state.contactGather[data.contactId]
      : null,
  };
};

const setToken = (state: ChatState, token: string): ChatState => {
  return {
    ...state,
    token,
  };
};

const clearUser = (state: ChatState): ChatState => {
  return {
    ...state,
    token: '',
    user: emptyUser,
  };
};

const setConference = (
  state: ChatState,
  conference: ConferenceData,
): ChatState => {
  return {
    ...state,
    conference: {
      data: { ...conference },
      joined: conference?.userId === state.user.userId,
      ringPlayed: conference?.userId !== state.user.userId,
    },
  };
};

const pauseConference = (
  state: ChatState,
  conference: ConferenceData,
): ChatState => {
  if (state.conference.data?.id !== conference?.id) return state;
  return {
    ...state,
    conference: {
      data: { ...state.conference.data },
      joined: false,
      ringPlayed: false,
    },
  };
};

const stopConference = (
  state: ChatState,
  conference: ConferenceData,
): ChatState => {
  if (state.conference.data?.id !== conference?.id) return state;
  return {
    ...state,
    conference: { data: null, joined: false, ringPlayed: false },
  };
};

function chatReducer(state: ChatState, action: Action): ChatState {
  switch (action.type) {
    case 'SET_GROUP_GATHER':
      return {
        ...state,
        groupGather: {
          ...state.groupGather,
          [(action.payload as Group)
            .groupId]: action.payload as Group,
        },
      };
    case 'SET_CONTACT_GATHER':
      return {
        ...state,
        contactGather: {
          ...state.contactGather,
          [(action.payload as Contact)
            .userId]: action.payload as Contact,
        },
      };
    case 'DEL_GROUP':
      return delGroup(state, action.payload as number);
    case 'DEL_GROUP_MEMBER':
      return delGroupMember(state, action.payload as GroupMap);
    case 'DEL_CONTACT':
      return delContact(state, (action.payload as Contact).userId);
    case 'SET_USER_GATHER':
      return {
        ...state,
        userGather: {
          ...state.userGather,
          [(action.payload as User).userId]: action.payload as User,
        },
      };
    case 'UPDATE_ACTIVE_ROOM':
      return { ...state, activeRoom: getActiveRoom(state) };
    case 'SET_ACTIVE_ROOM':
      return setActiveRoom(state, action.payload as SetActiveRoom);
    case 'USER_ONLINE':
      return setUserOnline(state, action.payload as number, 1);
    case 'USER_OFFLINE':
      return setUserOnline(state, action.payload as number, 0);
    case 'ADD_GROUP_MESSAGE':
      return addGroupMessage(state, action.payload as GroupMessage);
    case 'ADD_PRIVATE_MESSAGE':
      return addPrivateMessage(
        state,
        action.payload as PrivateMessage,
      );
    case 'ADD_GROUP_UNREAD_GATHER':
      return groupUnreadGather(
        state,
        action.payload as number,
        (x?: number) => (x || 0) + 1,
      );
    case 'ADD_CONTACT_UNREAD_GATHER':
      return contactUnreadGather(
        state,
        action.payload as number,
        (x?: number) => (x || 0) + 1,
      );
    case 'SET_TYPING':
      return { ...state, typing: action.payload as SetTyping };
    case 'LOSE_GROUP_UNREAD_GATHER':
      return groupUnreadGather(
        state,
        action.payload as number,
        () => 0,
      );
    case 'LOSE_CONTACT_UNREAD_GATHER':
      return contactUnreadGather(
        state,
        action.payload as number,
        () => 0,
      );
    case 'REVOKE_MESSAGE':
      return revokeMessage(state, action.payload as MessageOperation);
    case 'UPDATE_GROUP_INFO':
      return updateGroupInfo(state, action.payload as Group);
    case 'UPDATE_USER_INFO':
      return updateUserInfo(state, action.payload as User);
    case 'ADD_GROUP_MEMBER':
      return addGroupMember(state, action.payload as GroupMember);
    case 'SET_CONFERENCE':
      return setConference(state, action.payload as ConferenceData);
    case 'JOIN_CONFERENCE':
      return {
        ...state,
        conference: {
          data: { ...(action.payload as ConferenceData) },
          joined: true,
          ringPlayed: false,
        },
      };
    case 'PAUSE_CONFERENCE':
      return pauseConference(state, action.payload as ConferenceData);
    case 'STOP_CONFERENCE':
      return stopConference(state, action.payload as ConferenceData);
    case 'MARK_PRIVATE_MESSAGES_READ':
      return markPrivateMessagesRead(state, action.payload as number);
    case 'ADD_PRIVATE_MESSAGES':
      return addPrivateMessages(
        state,
        action.payload as AddPrivateMessages,
      );
    case 'ADD_GROUP_MESSAGES':
      return addGroupMessages(
        state,
        action.payload as AddGroupMessages,
      );
    case 'SET_LOADING':
      return {
        ...state,
        loading: action.payload as boolean,
        error: '',
      };
    case 'SET_ERROR':
      return {
        ...state,
        error: action.payload as string,
        success: undefined,
      };
    case 'SET_SUCCES':
      return {
        ...state,
        success: action.payload as string,
        error: undefined,
      };
    case 'SET_TOKEN':
      return setToken(state, action.payload as string);
    case 'SET_USER':
      return {
        ...state,
        user: action.payload as User,
      };
    case 'CLEAR_USER':
      return clearUser(state);
    case 'CLEAR_CHAT_DATA':
      return {
        ...state,
        activeRoom: null,
        groupGather: {},
        userGather: {},
        contactGather: {},
        conference: {
          data: null,
          joined: false,
          ringPlayed: false,
        },
        typing: null,
      };
    case 'SET_OPERATORS':
      return {
        ...state,
        operators: action.payload as Contact[],
      };
    case 'SET_VISIT_DATA':
      return {
        ...state,
        visitData: action.payload as VisitData[],
      };
  }

  return state;
}

export type ChatDispatch = React.Dispatch<Action>;
const emptyDispatch: ChatDispatch = () => null;

type ChatProviderProps = {
  defLang: string | undefined;
  token: string;
  refreshToken: string;
  children: JSX.Element | JSX.Element[];
};

export const ChatContext = React.createContext({
  state: emptyChatState,
  dispatch: emptyDispatch,
});

export const ChatProvider: React.FC<ChatProviderProps> = (
  props: ChatProviderProps,
) => {
  emptyUser.langCode = props.defLang;

  const token = props.token;
  const refreshToken = props.token;
  const chatState: ChatState = {
    ...emptyChatState,
    token,
    refreshToken,
  };

  const [state, dispatch] = React.useReducer(chatReducer, chatState);

  return (
    <ChatContext.Provider value={{ state, dispatch }}>
      {props.children}
    </ChatContext.Provider>
  );
};
